$main-bg: #0B2434;
$font: #2B283A;


* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  color: $font;
}
.center {
  text-align: center;
  h2 {
    font-size: 2rem;
    font-weight: 600;
  }
  p {
    font-weight: 600;
    font-size: 1.2rem;
  }
}

.App {
  background-color: $main-bg;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  .game {
    display: flex;
    justify-content:space-around;
    align-items: center;
    flex-direction:column;
    background-color:#F5F5F5;
    max-width: 500px;
    min-height: 500px;
    padding: 1em ;
    border-radius: 10px;
    > * {margin-top: 1em;}
    .container {
      display: grid;
      place-content: center;
      grid-template-columns: repeat(5, 1fr);
      gap: 1.6em;
      .number {
        display: grid;
        place-items: center;
        place-content: center;
        // justify-content: center;
        // align-items: center;
        gap: 0px 5px;
        grid-template-columns: 10px 10px ;
        grid-template-rows: repeat(6, 10px);
        width: 63px;
        height: 63px;
        border-radius: 10px;
        font-size: 2rem;
        font-weight: 700;
        cursor: pointer;
        box-shadow: 0 4px 3px 1px  #4444443f ;
      }
    }
    button {
      padding: .4em 1.5em;
      border-radius: 7px;
      outline: none;
      border: none;
      font-weight: 700;
      font-size: 2rem;
      background-color: #5035FF;
      color: white;
      transition: .08s;
      box-shadow: 0px 2px 7px 5px  #44444457 ;
      cursor: pointer;
      &:active {
        transform: scale(0.9);
        box-shadow: inset 5px 4px 15px 5px  #2a2a2a83 ;
      }
    }
  }
}
.active {
  background-color: #59E391;
}
