* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  color: #2B283A;
}

.center {
  text-align: center;
}

.center h2 {
  font-size: 2rem;
  font-weight: 600;
}

.center p {
  font-weight: 600;
  font-size: 1.2rem;
}

.App {
  background-color: #0B2434;
  min-height: 100vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.App .game {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  background-color: #F5F5F5;
  max-width: 500px;
  min-height: 500px;
  padding: 1em;
  border-radius: 10px;
}

.App .game > * {
  margin-top: 1em;
}

.App .game .container {
  display: -ms-grid;
  display: grid;
  place-content: center;
  -ms-grid-columns: (1fr)[5];
      grid-template-columns: repeat(5, 1fr);
  gap: 1.6em;
}

.App .game .container .number {
  display: -ms-grid;
  display: grid;
  place-items: center;
  place-content: center;
  gap: 0px 5px;
  -ms-grid-columns: 10px 10px;
      grid-template-columns: 10px 10px;
  -ms-grid-rows: (10px)[6];
      grid-template-rows: repeat(6, 10px);
  width: 63px;
  height: 63px;
  border-radius: 10px;
  font-size: 2rem;
  font-weight: 700;
  cursor: pointer;
  -webkit-box-shadow: 0 4px 3px 1px  #4444443f;
          box-shadow: 0 4px 3px 1px  #4444443f;
}

.App .game button {
  padding: .4em 1.5em;
  border-radius: 7px;
  outline: none;
  border: none;
  font-weight: 700;
  font-size: 2rem;
  background-color: #5035FF;
  color: white;
  -webkit-transition: .08s;
  transition: .08s;
  -webkit-box-shadow: 0px 2px 7px 5px  #44444457;
          box-shadow: 0px 2px 7px 5px  #44444457;
  cursor: pointer;
}

.App .game button:active {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  -webkit-box-shadow: inset 5px 4px 15px 5px  #2a2a2a83;
          box-shadow: inset 5px 4px 15px 5px  #2a2a2a83;
}

.active {
  background-color: #59E391;
}
